import '../style.css';

const About = () => {
    return (
        <>
            <div className='page-content'>
                <div className='about'>
                    <img src={require('./images/me.jpg')} alt="Me" />
                    <div className='paragraph'>
                        <p>
                            I'm a software developer based in Toronto, Ontario.
                        </p>
                        <p>
                            Currently, I'm a product engineering intern at Ontario Teachers' Pension Plan.
                        </p>
                        <p>
                            I've been into photography since high school, and recently I've been playing around with a new film camera.
                        </p>
                        <p>
                            I enjoy gaming, especially puzzle and strategy games like Tetris, Minesweeper, Balatro, and Slay the Spire. I'm also always reading something — whether that be a book, manga, or webtoon. My goal is to read 25 books for 2025.
                        </p>
                    </div>
                </div>
            </div>
        </>
    )
};

export default About; 